import React, { Component } from "react";
import axios from "axios";
import $ from "jquery";
import QrcodeDecoder from "qrcode-decoder";

class Scann extends Component {
  state = {
    file: null
  };

  uploadQR = () => {};

  handleUpload = e => {
    // console.log(e.target.files[0]);
    let file = e.target.files[0];
    this.setState({ file: file });

    const formData = new FormData();
    formData.append("qr", file);
    formData.append("qrt", "this.state.file");

    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    axios
      .post("http://localhost:8001/api", formData, config)
      .then(resp => {
        console.log("The file is successfully uploaded");
        // console.log(resp.data.src);

        let img = "http://localhost:8001/api/" + resp.data.src;

        $("#qrImage").attr({
          src: img
        });
        $("#qrImage").css({ display: "block" });

        // let qr = QrcodeDecoder;
        console.log(QrcodeDecoder);
        QrcodeDecoder.decodeFromImage(img).then(res => {
          console.log(res);
        });
      })
      .catch(error => {
        console.log(error);
      });
  };
  test = () => {
    let qr = QrcodeDecoder;
    console.log(QrcodeDecoder);
    let img = "http://localhost:8001/api/upload/26323.png";
    QrcodeDecoder.decodeFromImage(img).then(res => {
      console.log(res);
    });
  };

  render() {
    return (
      <div className="card">
        <img
          src="..."
          className="card-img-top"
          alt="..."
          style={{ display: "none" }}
          id="qrImage"
        />
        <div id="res" />
        <div className="card-body">
          <form onChange={this.handleUpload}>
            <div className="form-group">
              <label htmlFor="exampleFormControlFile1">
                Example file input
              </label>
              <input
                type="file"
                className="form-control-file"
                id="exampleFormControlFile1"
              />
            </div>
          </form>
          <button className="btn btn-danger" onClick={this.test}>
            test
          </button>
        </div>
      </div>
    );
  }
}

export default Scann;
